import React, { useState, useEffect, useCallback } from 'react';
import {
  Box,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  CircularProgress,
  Alert,
  Chip,
  Tooltip,
} from '@mui/material';
import {
  Delete as DeleteIcon,
  Visibility as VisibilityIcon,
  Upload as UploadIcon,
  Map as MapIcon,
} from '@mui/icons-material';
import { useDropzone } from 'react-dropzone';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { worksheetService } from '../services/api';
import { useAuth } from '../contexts/AuthContext';

const WorkSheets = () => {
  const [worksheets, setWorksheets] = useState([]);
  const [loading, setLoading] = useState(true);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [selectedWorksheet, setSelectedWorksheet] = useState(null);
  const [uploadDialogOpen, setUploadDialogOpen] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(false);
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();
  const { user } = useAuth();

  const hasImportPermission = ['SYSADMIN', 'SMBO'].includes(user?.role);
  const hasRemovePermission = ['SYSADMIN', 'SMBO'].includes(user?.role);
  const hasViewDetailedPermission = ['SYSADMIN', 'SMBO', 'SDVBO'].includes(user?.role);

  const fetchWorksheets = useCallback(async () => {
    try {
      setLoading(true);
      const response = await worksheetService.getAll();
      setWorksheets(response.data || []);
    } catch (error) {
      enqueueSnackbar('Erro ao carregar folhas de obra', { variant: 'error' });
      console.error('Error fetching worksheets:', error);
    } finally {
      setLoading(false);
    }
  }, [enqueueSnackbar]);

  useEffect(() => {
    fetchWorksheets();
  }, [fetchWorksheets]);

  const handleDelete = async () => {
    if (!selectedWorksheet) return;
    
    try {
      await worksheetService.delete(selectedWorksheet.id);
      enqueueSnackbar('Folha de obra removida com sucesso', { variant: 'success' });
      fetchWorksheets();
    } catch (error) {
      enqueueSnackbar('Erro ao remover folha de obra', { variant: 'error' });
      console.error('Error deleting worksheet:', error);
    } finally {
      setDeleteDialogOpen(false);
      setSelectedWorksheet(null);
    }
  };

  const handleFileUpload = async (file) => {
    setUploadProgress(true);
    
    try {
      const fileContent = await file.text();
      const geoJson = JSON.parse(fileContent);
      
      if (geoJson.type !== 'FeatureCollection' && geoJson.type !== 'Feature') {
        throw new Error('Ficheiro GeoJSON invlido');
      }

      await worksheetService.create(geoJson);
      enqueueSnackbar('Folha de obra importada com sucesso', { variant: 'success' });
      fetchWorksheets();
      setUploadDialogOpen(false);
    } catch (error) {
      if (error instanceof SyntaxError) {
        enqueueSnackbar('Ficheiro invlido. Por favor, selecione um ficheiro GeoJSON vlido', { variant: 'error' });
      } else {
        enqueueSnackbar(error.message || 'Erro ao importar folha de obra', { variant: 'error' });
      }
      console.error('Error uploading worksheet:', error);
    } finally {
      setUploadProgress(false);
    }
  };

  const onDrop = useCallback((acceptedFiles) => {
    if (acceptedFiles.length > 0) {
      handleFileUpload(acceptedFiles[0]);
    }
  }, []);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'application/geo+json': ['.geojson'],
      'application/json': ['.json']
    },
    maxFiles: 1,
  });

  const handleView = (worksheet) => {
    navigate(`/dashboard/worksheet/${worksheet.id}`);
  };

  const formatDate = (dateString) => {
    if (!dateString) return '-';
    const date = new Date(dateString);
    return date.toLocaleDateString('pt-PT');
  };

  return (
    <Box>
      <Box sx={{ mb: 3, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h4" sx={{ fontWeight: 'bold', color: 'primary.main' }}>
          Folhas de Obra
        </Typography>
        {hasImportPermission && (
          <Button
            variant="contained"
            startIcon={<UploadIcon />}
            onClick={() => setUploadDialogOpen(true)}
            sx={{ borderRadius: 2 }}
          >
            Importar GeoJSON
          </Button>
        )}
      </Box>

      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
          <CircularProgress />
        </Box>
      ) : worksheets.length === 0 ? (
        <Alert severity="info">
          Nenhuma folha de obra encontrada.
        </Alert>
      ) : (
        <TableContainer component={Paper} sx={{ borderRadius: 2 }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>ID</TableCell>
                <TableCell>Nome</TableCell>
                <TableCell>Tipo</TableCell>
                <TableCell>Estado</TableCell>
                <TableCell>Data de Criao</TableCell>
                <TableCell>N de Features</TableCell>
                <TableCell align="right">Aes</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {worksheets.map((worksheet) => (
                <TableRow key={worksheet.id} hover>
                  <TableCell>{worksheet.id}</TableCell>
                  <TableCell>{worksheet.name || 'Sem nome'}</TableCell>
                  <TableCell>{worksheet.type || '-'}</TableCell>
                  <TableCell>
                    <Chip
                      label={worksheet.status || 'Ativo'}
                      size="small"
                      color={worksheet.status === 'active' ? 'success' : 'default'}
                    />
                  </TableCell>
                  <TableCell>{formatDate(worksheet.createdAt)}</TableCell>
                  <TableCell>{worksheet.featuresCount || 0}</TableCell>
                  <TableCell align="right">
                    <Tooltip title="Ver detalhes">
                      <IconButton
                        onClick={() => handleView(worksheet)}
                        color="primary"
                      >
                        <VisibilityIcon />
                      </IconButton>
                    </Tooltip>
                    {worksheet.geometry && (
                      <Tooltip title="Ver no mapa">
                        <IconButton
                          onClick={() => navigate('/dashboard/map', { state: { worksheetId: worksheet.id } })}
                          color="secondary"
                        >
                          <MapIcon />
                        </IconButton>
                      </Tooltip>
                    )}
                    {hasRemovePermission && (
                      <Tooltip title="Remover">
                        <IconButton
                          onClick={() => {
                            setSelectedWorksheet(worksheet);
                            setDeleteDialogOpen(true);
                          }}
                          color="error"
                        >
                          <DeleteIcon />
                        </IconButton>
                      </Tooltip>
                    )}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      <Dialog open={deleteDialogOpen} onClose={() => setDeleteDialogOpen(false)}>
        <DialogTitle>Confirmar Remoo</DialogTitle>
        <DialogContent>
          <Typography>
            Tem certeza que deseja remover a folha de obra "{selectedWorksheet?.name || 'Sem nome'}"?
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialogOpen(false)}>Cancelar</Button>
          <Button onClick={handleDelete} color="error" variant="contained">
            Remover
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog open={uploadDialogOpen} onClose={() => !uploadProgress && setUploadDialogOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle>Importar Folha de Obra (GeoJSON)</DialogTitle>
        <DialogContent>
          <Box
            {...getRootProps()}
            sx={{
              border: '2px dashed',
              borderColor: isDragActive ? 'primary.main' : 'grey.300',
              borderRadius: 2,
              p: 4,
              textAlign: 'center',
              cursor: 'pointer',
              bgcolor: isDragActive ? 'action.hover' : 'background.paper',
              transition: 'all 0.3s',
              '&:hover': {
                borderColor: 'primary.main',
                bgcolor: 'action.hover',
              },
            }}
          >
            <input {...getInputProps()} />
            {uploadProgress ? (
              <CircularProgress />
            ) : (
              <>
                <UploadIcon sx={{ fontSize: 48, color: 'text.secondary', mb: 2 }} />
                <Typography variant="h6" gutterBottom>
                  {isDragActive ? 'Largue o ficheiro aqui' : 'Arraste um ficheiro GeoJSON aqui'}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  ou clique para selecionar
                </Typography>
                <Typography variant="caption" color="text.secondary" sx={{ mt: 1, display: 'block' }}>
                  Formatos aceites: .geojson, .json
                </Typography>
              </>
            )}
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setUploadDialogOpen(false)} disabled={uploadProgress}>
            Cancelar
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default WorkSheets;